export default {
	data() {
		return {
			currentQN : 0, // 当前题目
			swiperHeight : 200,
			qns : [], // 问卷数据
		};
	},
	onLoad:function(){
		_self = this;
		// 加载问卷信息
		uni.request({
		  url: 'https://www.easy-mock.com/mock/5bb833775df5622d84ac87ca/example/qn#!method=get',
		  success:function(res){
			_self.qns = res.data.data;
		  }
		});
		// 计算屏幕高度
		wx,wx.getSystemInfo({
		  success: function(res) {
			// swiper 高度 = 屏幕高度 - 顶部步骤高度 - 底部按钮高度
			_self.swiperHeight = res.screenHeight - 180;
		  }
		});
	},
	methods:{
		swiperChange : function(e){
			this.currentQN = e.detail.current;
		  },
		  formSubmit : function(e){
			//检查数据
			var data = e.detail.value;
			var checkError = false;
			var errorIndex = 0;
			for(var k in data){
			  if(data[k] == '' || data[k].length < 1){
				console.log('no');
				checkError = k;
				break;
			  }
			  errorIndex++;
			}
			if (checkError){
			  uni.showToast({
				title: '请完善选择',
				icon: "none"
			  });
			  _self.currentQN = errorIndex;
			  return ;
			}
			uni.showToast({
			  title: '请观察控制台',
			});
			console.log(data);
		  },
		  next : function(){
			this.currentQN++;
		  }
	}
}
